/*
 * INBOX
 */
.table-wrap {
	overflow: auto; 

}
.inbox-body {position:relative}

.inbox-body .table-wrap {
	background:@white;
	padding:10px 14px 7px; 
	position:relative;
	margin-left:200px;
	overflow-x:hidden;
}
.inbox-body.no-content-padding {
	margin-top:0;
	background:@white;
	overflow:hidden;
}
	
.inbox-nav-bar {
	height: 70px;
	margin-bottom: 0;
	/*border-bottom: 1px solid #CECECE;*/
	padding: 20px 14px;
	background:@white;
		
}
	
.inbox-nav-bar .page-title {
	display: inline-block;
	margin: 0;
	width: 196px;
	line-height: 33px;
	vertical-align: middle;
}
	
.inbox-footer {
	position: absolute;
	bottom: -53px;
}
.inbox-paging {
	margin-left:10px;
}
	
.inbox-footer .btn-group {
	margin-left:10px;
}

#inbox-table {
	font-size: 13px;
	border-top: none;
	border-left-color: transparent !important;
	border-right-color: transparent !important;
}

	
#inbox-table tbody tr:hover {
	cursor: pointer;
	background:#E4E4E4;
}
	
#inbox-table tr td {
	border-right: none;
	border-left: none;
	line-height:26px;
	padding:6px 4px 7px !important;
}
	
#inbox-table .inbox-table-icon {
	padding-left:15px !important;
}
	
#inbox-table tbody tr th {
	overflow: hidden;
}
	
#inbox-table.table tbody>tr>td {
	border-color:#fff !important;
}
	
#inbox-table .radio, #inbox-table .checkbox {
	margin-top:-1px;
	margin-bottom:0px;
}
	
.inbox-table-icon, .inbox-data-attachment {
	width: 28px;
	text-align: left;
	padding-left: 12px !important;
	padding-right: 0px !important;
}

.inbox-table-icon, .inbox-data-attachment > :first-child {
		
}

.inbox-data-from {
	width: 200px;
}
	
.inbox-data-from > :first-child {
	width: 200px;
	display:block;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.inbox-data-date {
	width: 80px;
	padding-left: 7px !important;
	padding-right: 0px !important;
}
.inbox-data-message > :first-child {
	width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	height: 27px;
	color:#8A8A8A;
} .inbox-data-message > :first-child span {
	color:#111;
}.inbox-data-message > :first-child span.label {
	color:@white;
}
	
.inbox-data-message > :first-child > :first-child:after {
	content: " - ";
}

	.unread td {
		background: @white;
	}
	
	.unread .inbox-data-message > :first-child > :first-child, .unread .inbox-data-from > :first-child, .unread .inbox-data-date > :first-child {
		font-weight: bold;
	}
	
	tr.highlight td, tr.unread.highlight td {
		background: #ffffcc !important; /*#ffffcc;*/
		color: @gray-dark;
	}
	
.inbox-checkbox-triggered {
	display:inline-block;
}
	
.inbox-checkbox-triggered > .btn-group {
	margin-right:10px;
}
	
.inbox-checkbox-triggered > .btn-group .btn {
	padding-left:14px;
	padding-right:14px;
}
	
.inbox-side-bar {
	height:100%; 
	position:absolute; 
	background:@white; 
	display:block; 
	width:200px; 
	padding: 10px 0 10px 14px;
	/*overflow-y: auto;
	overflow-x: hidden;*/
	-webkit-overflow-scrolling: touch;
	z-index: 1;
	
}

.inbox-side-bar::-webkit-scrollbar {
    width:0!important
}
	
.inbox-side-bar h6 {
	font-weight:normal;
	font-size:11px;
	display:block;
	padding:0px 15px;
	text-transform:uppercase;
	color: #838383;
}
.inbox-side-bar h6 a {
	font-size: @font-size-base + 1px;
	margin-top:-2px;
		
}
.inbox-side-bar h6 .tooltip {
	text-transform:none !important;
}

.inbox-side-bar > .btn {
	margin-bottom:35px;
}
.inbox-side-bar .input-group {
	margin-bottom:25px;
}
	
#compose-mail-mini {
	margin-left:4px;
}
	
.inbox-space {
	display:block;
	width:185px;
}
	
.inbox-space > .progress {
	margin-top:5px;
}

.inbox-menu-lg {
	list-style:none;
	padding:0;
	margin: 0 0 20px 0;
}.inbox-menu-lg li a {
	display:block;
	padding: 6px 15px 7px;
	font-size: @font-size-base;
	color:@gray-dark;
}.inbox-menu-lg li {
	display:block;
	width:100%;
}.inbox-menu-lg li a:hover {
	text-decoration:none;
	background: @gray-lightest;
}.inbox-menu-lg li.active a {
	font-weight:bold;
	background: #F0F0F0;
	border-bottom: 1px solid #E7E7E7;
	color:@blueStandard;
}

.inbox-menu-sm {
	list-style:none;
	padding:0;
	margin: 0 0 20px 0;
}.inbox-menu-sm li a {
	display:block;
	padding:8px 15px 10px;
	font-size: @font-size-base;
	color:@gray-dark;
}.inbox-menu-sm li {
	display:block;
	width:100%;
}.inbox-menu-sm li a:hover {
	text-decoration:none;
	background: @gray-lightest;
}.inbox-menu-sm li.active a {
	font-weight:bold;
	background: #F0F0F0;
	border-bottom: 1px solid #E7E7E7;
	color:@blueStandard;
}
	
/*
	* Email opened
	*/
	
.email-open-header {
	margin: -10px 0 0 0;
	font-size: 20px;
	border-bottom: 1px solid @gray-mid-light;
	border-top: 1px solid @gray-lighter;
	padding: 15px 3px;
} .email-open-header > span {
	font-size:10px;
	font-weight:normal;
	padding:3px 5px;
	letter-spacing:normal;
	text-transform: uppercase;
	vertical-align: middle;
	line-height: 33px;
	background:#ACACAC;
}
	 
.inbox-info-bar {
	padding:10px 0px;
	border-bottom: 1px solid @gray-mid-light;
}
	
.inbox-info-bar img {
	width: 35px;
	height: auto;
	display: inline-block;
	vertical-align: middle;
	margin-right: 7px;
	margin-left: 2px;
	border-left: 3px solid #FFF
}
	
.inbox-message, .inbox-download {
	padding:15px 4px;
	border-bottom: 1px solid @gray-mid-light;
}
	
.inbox-download-list {
	list-style:none;
	margin:5px 0 0;
	padding:0;
}.inbox-download-list li {
	display:inline-block;
	margin:0 5px 0 0;
	vertical-align:top;
}.inbox-download-list li>:first-child {
	margin-bottom:0;
	width: 150px;
	overflow:hidden;
}.inbox-download-list li>:first-child:hover {
	background:@white;
	border-color:#C0C0C0;
}
	
.inbox-download-list li>:first-child >:first-child  {
	text-align:center;
	display:block;
	color: #D6D6D6;
}.inbox-download-list li>:first-child >:first-child > .fa{
	font-size: 150px;
}
.inbox-download-list li>:first-child >:first-child > img {
	max-width:120px;
}
	
.inbox-compose-footer {
	padding:10px;
	background:#F5F5F5;
	border-bottom:1px solid #A9A9A9;
}
	
/* added space for large width devices */
	
	
.inbox-info-bar, .inbox-message, .inbox-download, .inbox-compose-footer {
	margin-right:240px;
	position:relative;
}

.email-infobox {
	display:block;
	width:180px;
	border-bottom:1px solid @gray-mid-light;
	padding-bottom:0px;
	padding-top:15px;
	position:absolute;
	top:65px;
	right:15px;
}
	
/* compose email */
	
.inbox-info-bar .form-group {
	margin:0;
}
	
.inbox-info-bar .form-group input, .inbox-info-bar .select2-container-multi .select2-choices {
	border-color: @white !important;
}

.inbox-info-bar .select2-selection--multiple {
	border-color: @white !important;
}
	
.inbox-info-bar .select2-choices > div {
	display:none
}
	
.inbox-info-bar .col-md-1, .inbox-info-bar .col-md-11 {
	padding-left:0px;
	padding-right:0px;
}
	
.fileinput {
	padding-top:3px;
}
	
.hidden {
	display:none;
}
	
.inbox-info-bar em {
	position: absolute;
	top: 6px;
	right: 20px;
	text-align:right;
	font-style:normal;
}
	
/* email reply text */
	
.email-reply-text > div {
	border-left:1px solid #D6D6D6;
	padding-left:10px;
	margin-left:50px;
	color:#A9A9A9;
}

/* reply text area */
	
.inbox-body .note-editor .note-editable	{
	height: 100% important;
}
	
.email-reply-text >:first-child {
	padding-left: 45px;
}
	
@media (max-width: 1280px) {
		
	.inbox-info-bar, .inbox-message, .inbox-compose-footer {
		margin-right:0px;
	}
		
	.email-infobox {
		display:none;
	}

}
	
@media (max-width: 1024px) {
	.inbox-side-bar {
		display:none !important;
	}
	.inbox-body .table-wrap {
		margin-left:0px !important;
	}
		
	.inbox-info-bar .col-md-1, .inbox-info-bar .col-md-11 {
		padding-left:26px;
		padding-right:26px;
	}
}

/* hide helper inside inbox as it has issues with z-index */

#inbox-content [data-event="showHelpDialog"],
#inbox-content [data-event="codeview"],
#inbox-content .note-insert {
	display:none !important;
}

#inbox-content [data-event="fullscreen"] {
	border-radius: @border-radius-base;
	-webkit-border-radius: @border-radius-base;
	-moz-border-radius: @border-radius-base;
}


/* adjust buttons */
#inbox-content .note-toolbar .btn-sm {
	padding: 2px 6px 3px;
	font-size: 14px;
}

/* adjust width of dropdowns */
#inbox-content .note-toolbar .note-para .dropdown-menu {
	min-width: 180px;
}
