
/*
 * VECTOR MAP
 */

.jvectormap-label {
  position: absolute;
  display: none;
  border: solid 1px #CDCDCD;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background: #292929;
  color: white;
  font-family: sans-serif, Verdana;
  font-size: smaller;
  padding: 3px;
}

.jvectormap-zoomin, .jvectormap-zoomout {
  position: absolute;
  background: #292929;
  padding: 4px;
  width: 22px;
  height: 22px;
  cursor: pointer;
  line-height: 10px;
  text-align: center;
  font-size: 14px;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  box-shadow: inset 0 -2px 0 rgba(red(@black), green(@black), blue(@black), 0.05);
  -moz-box-shadow: inset 0 -2px 0 rgba(red(@black), green(@black), blue(@black), 0.05);
  -webkit-box-shadow: inset 0 -2px 0 rgba(red(@black), green(@black), blue(@black), 0.05);
  background-color: @white;
  border: 1px solid @gray-mid-light;
}

.jvectormap-zoomin:hover, .jvectormap-zoomout:hover {
  background: @gray-lighter;
  border-color: lighten(@gray-light, 25%);
}

.jvectormap-zoomin {
  top: 0px;
}

.jvectormap-zoomout {
  top: 24px;
}

.vector-map {
  height: 300px;
  width: 100%;
  padding: 10px;
}

#heat-fill {
  display: block;
  position: relative;
  margin-bottom: 20px;
  background: @gray-dark;
  height: 7px;
  width: 200px;
  background-image: url(data:image/png;base64,)
}

.fill-a, .fill-b {
  width: 20px;
  text-align: right;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  background: @white;
  padding-right: 4px;
  position: absolute;
  left: 0px;
  margin-top: -7px;
  font-weight: bold;
}
.fill-b {
  text-align: left;
  position: absolute;
  right: 0px;
  left: auto;
  top: 0px;
  width: 60px;
  padding-left: 4px;
  padding-right: 0px;
}