/*
 * TODO's
 */

.todo-group-title {
	margin:0;
	line-height:31px;
	padding: 0 0 0 10px;
	background:lighten( @gray-lightest, 2.5%);
	border-bottom:1px solid darken(@gray-lighter, 3%);
	border-top:1px solid @gray-lightest;
	color:@gray-light;
}

.todo {
	margin:0;
	padding:0;
	min-height:5px;
	list-style:none;
}

.todo > li {
	display:block;
	position:relative;
	overflow:hidden;
	border-bottom:1px solid darken(@gray-lighter, 3%);
	margin:0 5px;
	background: url(data:image/png;base64,) 1px 11px no-repeat;

}

.todo > li:last-child, .todo > li:only-child {
	border-bottom:none;
}

.todo > li >:first-child {
	display: block;
	border-right: 1px solid #FFE1EB;
	height: 100%;
	padding: 6px 11px 6px 18px;
	width: 20px;
	vertical-align: top;
	position: absolute;
}.todo > li >:first-child:hover {
	cursor: move;
}.todo > li.complete >:first-child:hover {
	cursor:default;
}.todo > li.complete {
	background:none;
}

.todo > li > p {
	height: 100%;
	margin-left: 52px;
	border-left: 1px solid #FFE1EB;
	display: inline-block;
	padding: 8px 0px 6px 7px;
	margin-bottom: 0;
	min-height: 37px;
	line-height: normal;
	font-size:14px;
	font-weight: 500;
	color: @gray-dark;
}

.todo > li > p > span {
	display:block;
	line-height:12px;
	font-size:10px;
	font-weight: normal;
}
.todo > li > p >:first-child {
	margin-top:-5px;
	color:@gray-light;
	margin-bottom:4px;
}
.todo > li > p > .date {
	color:@gray-mid-light;
}

.todo > li.complete >* {
	text-decoration:line-through;
	font-style:italic
}

/* drag */
.todo > li.ui-sortable-helper {
	border-top:1px solid @gray-lighter;
	background:rgba(113, 132, 63,0.1);
}
