html {
	margin: 0;
	padding: 0;
	min-height: 100%;
	background: url('@{base-url}/@{html-background-image}') @html-background-color;
	position: relative;
}

body {
	margin: 0;
	padding: 0;
	min-height: 100%;
	background: url('@{base-url}/@{body-background-image}') @body-background-color;
	direction: ltr;
}

/* when mobile menu is active */
body.mobile-view-activated.hidden-menu {
	overflow-x: hidden;
}

body.modal-open {
	overflow: hidden !important;
}

a:hover, a:active, a:focus, button, button:active, button:focus, object, embed, input::-moz-focus-inner {
	outline: 0;
}

h1, h2, h3, h4 {
	margin: 0;
	font-family: "Open Sans", Arial, Helvetica, Sans-Serif;
	font-weight: 300;
}

.page-title {
	margin: 12px 0 28px; 
	span {
		font-size: @font-size-base+3;
		color: @gray-dark;
		display: inline-block;
		vertical-align: 1px;
	}
}

label {
	font-weight: normal;
}

*:focus {
	outline: 0 !important;
}

/*
 * IE FIX for tap delay
 */
a, input, button {
	-ms-touch-action: none !important;
}

textarea:focus, select:focus, input[type="text"]:focus, input[type="password"]:focus, input[type="datetime"]:focus, input[type="datetime-local"]:focus, input[type="date"]:focus, input[type="month"]:focus, input[type="time"]:focus, input[type="week"]:focus, input[type="number"]:focus, input[type="email"]:focus, input[type="url"]:focus, input[type="search"]:focus, input[type="tel"]:focus, input[type="color"]:focus, .uneditable-input:focus {
	outline: 0;
	outline: thin dotted \9;
	/* IE6-9 */

	-webkit-box-shadow: none !important;
	-moz-box-shadow: none !important;
	box-shadow: none !important;
}

.input-sm, .input-lg, .input-xs, .form-control {
	border-radius: 0px !important;
	-webkit-border-radius: 0px !important;
	-moz-border-radius: 0px !important;
}

.input-xs {
	height: 24px;
	padding: 2px 10px;
	font-size: 11px;
	line-height: 1.5;
}

.btn-xs {
	padding: 1px 5px;
}

.btn-sm {
	padding: 6px 10px 5px;
}

.btn-lg {
	padding: 10px 16px;
}

.no-space {
	margin: 0;
}

.no-space > [class*="col-"] {
	margin: 0 !important;
	padding-right: 0;
	padding-left: 0;
}

#content {
	padding: 10px 14px;
	position: relative;
	/*position:relative;
	 left:0px;*/
}

body.container {
	position: absolute;
	left: 0;
	right: 0;
	padding: 0;
	border-left: 1px solid @gray-mid-light;
	border-right: 1px solid @gray-mid-light;
	box-shadow: 0 2px 70px rgba(red(@black), green(@black), blue(@black), 0.45);
}

body.container.hidden-menu:not(.mobile-view-activated) {
	overflow:hidden;
}

body.container.modal-open {
	padding:0px !important;
}

/*
 * Headers
 */

h1 {
	letter-spacing: -1px;
	font-size: @font-size-h1;
	margin: 10px 0;
}
h1 small {
	font-size: @font-size-h4;
	font-weight: 300;
	letter-spacing: -1px;
}
.lead {
	font-size: @font-size-h4  + 1px;
}

h2 {
	letter-spacing: -1px;
	font-size: @font-size-h2;
	margin: 20px 0;
	line-height: normal;
}

h3 {
	display: block;
	font-size: @font-size-h3;
	font-weight: 400;
	margin: 20px 0;
	line-height: normal;
}

h4 {
	line-height: normal;
}

h5 {
	font-size: @font-size-h5;
	font-weight: 300;
	margin: 10px 0;
	line-height: normal;
}

h6 {
	font-size: @font-size-h6;
	margin: 10px 0;
	font-weight: bold;
	line-height: normal;
}

.row-seperator-header {
	margin: 15px 14px 20px;
	border-bottom: none;
	display: block;
	color: darken( @gray-light, 20.9%);
	font-size: 20px;
	font-weight: 400;
}

/*
 * Footer
 * see: main-page-footer.less
 */

/*
 * Center Canvas
 */
.center-canvas, .center-child-canvas > canvas {
	display: block !important;
	margin: 0 auto !important;
}

/*
 * JUST A BORDER
 */
.bordered {
	border: 1px solid rgba(red(@black), green(@black), blue(@black),.2);
}
.bordered:hover {
	border: 1px solid rgba(red(@black), green(@black), blue(@black),.4);
}

/*
 * ***************************************************
 * MAIN LAYOUT START
 * ***************************************************
 */

aside {
	display: block;
	/*overflow: hidden;*/
	background: @asideColor-end;
	background: url(@asideDataURL);
	background: -moz-linear-gradient(left, @asideColor-end 93%, @asideColor-start 100%);
	background: -webkit-gradient(linear, left top, right top, color-stop(93%, @asideColor-end), color-stop(100%, @asideColor-start));
	background: -webkit-linear-gradient(left, @asideColor-end 93%, @asideColor-start 100%);
	background: -o-linear-gradient(left, @asideColor-end 93%, @asideColor-start 100%);
	background: -ms-linear-gradient(left, @asideColor-end 93%, @asideColor-start 100%);
	background: linear-gradient(to right, @asideColor-end 93%, @asideColor-start 100%);
	min-height: 100%;
}

/*
 * HEADER GROUP
 */

#header {
	display: block;
	height: @smart-navbar-height;
	margin: 0;
	padding: 0 13px 0 0;
	background-color: @smart-navbar-color-start;
	background-image: -moz-linear-gradient(top, @smart-navbar-color-start, @smart-navbar-color-end);
	background-image: -webkit-gradient(linear, 0 0, 0 100%, from(@smart-navbar-color-start), to(@smart-navbar-color-end));
	background-image: -webkit-linear-gradient(top, @smart-navbar-color-start, @smart-navbar-color-end);
	background-image: -o-linear-gradient(top, @smart-navbar-color-start, @smart-navbar-color-end);
	background-image: linear-gradient(to bottom, @smart-navbar-color-start, @smart-navbar-color-end);
	background-repeat: repeat-x;
	position: relative;
	z-index:  @left-panel-zindex+1;
}

#header > div {
	display: inline-block;
	vertical-align: middle;
	height: @smart-navbar-height;
	float: left;
}

#header > div.open {
	font-weight: bold;
}

.fixed-header #header {
	position: fixed;
	width: 100%;
	z-index:  @left-panel-zindex+1;
	top: 0px;
	border-bottom: 2px solid @gray-light;
	border-bottom: 1px solid rgba(0,0,0,.3);
	box-shadow: -11px 12px 23px rgba(0, 0, 0, 0.1);
}

/* as per version 1.3 */
.fixed-header.fixed-ribbon #header {
	border-bottom: none;
	box-shadow: none;
}

.container.fixed-header #header {
	max-width: 1164px;
}

.fixed-header #header .ajax-dropdown {
	z-index:  @left-panel-zindex+2;
}

.fixed-header #shortcut {
	position: fixed;
}

.fixed-header #main {
	margin-top: @smart-navbar-height;
}

#logo-group > span {
	display: inline-block;
	height: @smart-navbar-height  - 10;
	float: left;
}

#logo-group span#activity {
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	border-radius: 2px;
	cursor: default !important;
	display: inline-block;
	font-weight: bold;
	height: 24px;
	width: 24px;
	padding: 2px;
	text-align: center;
	text-decoration: none !important;
	-moz-user-select: none;
	-webkit-user-select: none;
	background-color: lighten(@gray-lightest, 1.9%);
	background-image: -webkit-gradient(linear,left top,left bottom,from(lighten(@gray-lightest, 1.9%)),to(darken(@gray-lightest, 1%)));
	background-image: -webkit-linear-gradient(top,lighten(@gray-lightest, 1.9%),darken(@gray-lightest, 1%));
	background-image: -moz-linear-gradient(top,lighten(@gray-lightest, 1.9%),darken(@gray-lightest, 1%));
	background-image: -ms-linear-gradient(top,lighten(@gray-lightest, 1.9%),darken(@gray-lightest, 1%));
	background-image: -o-linear-gradient(top,lighten(@gray-lightest, 1.9%),darken(@gray-lightest, 1%));
	background-image: linear-gradient(top,lighten(@gray-lightest, 1.9%),darken(@gray-lightest, 1%));
	border: 1px solid @gray-mid-light;
	color: @dropdownbtn-icon-color;
	font-size: 19px;
	margin: 10px 0 0 0;
	position: relative;
}

#logo-group span#activity:hover {
	border: 1px solid @gray-mid-light;
	transition: all 0.0s;
	cursor: pointer !important;
	-webkit-box-shadow: inset 0px 0px 4px 1px rgba(red(@black), green(@black), blue(@black), 0.08);
	box-shadow: inset 0px 0px 4px 1px rgba(red(@black), green(@black), blue(@black), 0.08);
}

#logo {
	display: inline-block;
	width: @asideWidth  - 45px;
	margin-top: 13px;
	margin-left: 9px;
}

#logo img {
	width: 110px;
	height: auto;
	padding-left: 3px;
}

#activity b.badge {
	position: absolute;
	top: -5px;
	right: -5px;
	cursor: pointer;
	background: @blueSky;
	display: inline-block;
	font-size: 10px;
	box-shadow: inset 1px 1px 0 rgba(red(@black), green(@black), blue(@black), 0.1), inset 0 -1px 0 rgba(red(@black), green(@black), blue(@black), 0.07);
	color: @white;
	font-weight: bold;
	border-radius: 50%;
	-moz-border-radius: 50%;
	-webkit-border-radius: 50%;
	padding: 2px 4px 3px 4px;
	text-align: center;
	line-height: normal;
}
#activity.active .badge {
	background: darken(@blueSky, 10%) !important;
}

#project-context, .project-context {
	display: inline-block;
	padding: 7px 13px 0;
	position: relative;
}

#project-context > span, .project-context > span {
	display: block;
}

#project-context > :first-child, .project-context > :first-child {
	display: block;
	color: @gray-mid-light;
	font-size: 10px;
	font-weight: bold;
	text-transform: uppercase;
	text-shadow: 0 0 1px white;
	padding-left: 0px;
	text-align: left;
}

#project-selector, .project-selector {
	max-width: 97%;
	white-space: nowrap;
	background: none;
	border: none;
	padding: 0;
	text-align: left;
	color: @gray;
	font-size: 14px;
	cursor: pointer;
}

#project-selector:hover, .project-selector:hover {
	color: @gray-dark;
}

.header-search {
	position: relative;
}
.header-search.pull-right {
	margin-left: 6px;
}

.header-search > input[type="text"] {
	display: block;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	min-width: 200px;
	width: 100%;
	height: 30px;
	padding: 0 10px;
	outline: none;
	border-width: 1px;
	border-style: solid;
	border-radius: 0;
	border-color: @gray-mid-light;
	background: @white;
	color: #404040;
	appearance: normal;
	-moz-appearance: none;
	-webkit-appearance: none;
	margin-top: 10px;
	line-height: normal;
}

.header-search > button {
	background: none;
	border: none;
	color: #6D6A69;
	font-size: 17px;
	height: 30px;
	line-height: 30px;
	margin: 0;
	padding: 0;
	position: absolute;
	right: 0px;
	top: 10px;
	width: 30px;
	z-index: 2;
}

#search-mobile {
	display: none;
}

#cancel-search-js {
	position: absolute;
	display: none;
	background: @brand-danger;
	z-index: 3;
	color: @white;
	padding: 0;
	right: 10px;
	top: 10px;
	text-align: center;
	height: 29px;
	line-height: 29px;
	width: 30px;
	font-size: 17px;
	text-decoration: none !important;
}

#header > :first-child, aside {
	width: @asideWidth;
}

/*
 * LEFT PANEL
 */

#left-panel {
	position: absolute;
	top: 0px;
	left: 0px;
	z-index: @left-panel-zindex;
	padding-top: @smart-navbar-height;
}

/*
 * MAIN PANEL
 */

#main {
	margin-left: @asideWidth;
	padding: 0;
	padding-bottom: @smart-footer-height;
	min-height: 500px;
	position: relative;
}

/*
 * RIBBON
 */

#ribbon {
	min-height: @ribbon-height;
	background: @ribbonBGColor;
	padding: 0 13px;
	position: relative;
}

#ribbon .breadcrumb {
	display: inline-block;
	margin: 0;
	padding: 11px 34px 11px 0 !important;
	background: none;
	vertical-align: top;
}

#ribbon .breadcrumb a, #ribbon .breadcrumb {
	color: #BBB !important;
	text-decoration: none !Important;
}

#ribbon .breadcrumb > .active, #ribbon .breadcrumb li:last-child {
	color: #E4E4E4;
}

.fixed-ribbon #ribbon {
	position: fixed;
	right: 0;
	left: @asideWidth;
	top: @smart-navbar-height;
	z-index: @fixed-ribbon-zindex;
}

.container.fixed-ribbon #ribbon {
	width: 944px;
	left: 590px;
}

.fixed-ribbon #content {
	padding-top: @smart-navbar-height  + 1;
}

.minified.fixed-ribbon #ribbon {
	left: 45px;
}

.hidden-menu.fixed-ribbon #ribbon {
	left: 10px;
}

/*
 * NAV
 * Menu CSS is learnt from: http://experiments.wemakesites.net/css3-treeview.html
 */

.fixed-navigation #left-panel {
	height: 100%;
	position: fixed;
	/*z-index: @fixed-navigation-zindex; */
}

/* Add this if not using slimscroll*/

.fixed-navigation nav {
	-webkit-overflow-scrolling: touch;
	overflow-y: auto;
	height: 100%;
	padding-bottom: 49px;
}

.fixed-navigation nav > ul {
	padding-right: 15px;
	position: relative;
	height: 100%;
	width: 114%;
	overflow-x: hidden;
	overflow-y: scroll;
}

.mobile-detected.fixed-navigation nav > ul {
	padding-right: 30px !important;
}

/* Slimscrol CSS change: delete this if not using slimscroll

 .fixed-navigation nav {
 padding-bottom: 49px;
 }

 .fixed-navigation nav > ul {
 position: relative;
 }

 .slimScrollBar,
 .slimScrollRail {
 display:none !important;
 }

 .fixed-navigation .slimScrollBar,
 .fixed-navigation .slimScrollRail {
 display:block !important;
 }

 .minified .slimScrollBar {
 display:none !important;
 }*/

/* end slimscroll css change*/

.minified.fixed-navigation nav {
	-webkit-overflow-scrolling: none;
	overflow: visible;
	height: auto;
	padding-bottom: 0px;
}

.minified.fixed-navigation nav > ul {
	overflow: visible;
	padding-right: 0px;
	width: 100%;
}

/* All levels */
nav ul li a:active {
	background: @asideNavActiveBGColor !important;
}

/* All levels */
nav ul {
	width: 100%;
	padding: 40px 28px 25px 0;
	padding: 0;
	margin: 0;
	font-size: 13px;
	line-height: 0.5em;
	list-style: none;
	position: relative;
}

/* All levels */
nav ul .active > a {
	color: #fff !important;
	position: relative;
}

/* All levels */
nav ul li.active > a:before {
	content: "\f0d9";
	font-family: FontAwesome;
	display: block;
	height: 27px;
	line-height: normal;
	width: 27px;
	position: absolute;
	right: -21px;
	font-size: 20px;
	color: @gray-lighter;
}

nav ul li.active.open > a:before {
	content: "";
}

nav > ul > li:last-child.open {
	border-bottom: 1px solid rgba(255,255,255,0.15);
}

/* All levels 
nav ul li {
	overflow: hidden;
}*/

/* All levels */
nav ul li a {
	line-height: normal;
	font-size: @asideNavFontSize;
	padding: 10px 10px 10px 11px;
	color: @asideLinkColor;
	display: block;
	font-weight: normal;
	text-decoration: none !important;
	position: relative;
}

/* Second level */
nav > ul > li > ul > li > a {
	padding-top: 7px;
	padding-bottom: 7px;
}

/* Third Level */
nav > ul > li > ul > li > ul > li > a {
	padding-top: 7px;
	padding-bottom: 7px;
}

/* All levels */
nav ul li a:focus {
	color: @asideLinkColorFocus;
}

/* All levels */
nav ul li a:hover {
	color: @asideLinkColorHover;
	text-decoration: none;
}

nav ul li a.inactive,
nav ul li a.inactive:hover {
	opacity: .5;
	cursor: not-allowed;
	color: @asideLinkColor; 
}

/* All levels */
nav ul b {
	float: right;
	font-size: @asideExpanIconSize;
	margin-top: @asideExpanIconMT;
}

/* All levels */
nav ul span.menu-item-parent {
	display: inline-block;
	margin: 0;
	padding: 0;
}

/* First level: A, Second level: LI Open */
nav ul li.open > a, nav ul li.open > a b {
	color: #fff !important;
}

/* First level: Icon */
nav > ul > li > a b {
	position: absolute !important;
	right: @asideNavExpanIconPush-x;
	top: @asideNavExpanIconPush-y;
}

nav > ul > li > a > i {
	/*font-size: 121%; fa-lg overrides this so..  */
	margin-right: 5px;
	width: 15px;
	display: inline-block;
	text-align: center;
	position: relative;
}
/* First level icon */
nav > ul > li > a > i > em {
	font-size: 9px;
	display: block;
	padding: 2px;
	position: absolute;
	top: -8px;
	right: -6px;
	text-decoration: none;
	font-style: normal;
	background: #ED1C24;
	color: @white;
	min-width: 13px;
	border-radius: 50%;
	max-height: 13px;
	line-height: 8px;
	font-weight: bold;
	vertical-align: baseline;
	white-space: nowrap;
	text-align: center;
	border: 1px solid rgba(red(@white), green(@white), blue(@white), 0.1);
}

/* Second level switched to relative */
nav ul li li {
	border-bottom: none;
	position: relative;
}

/* Second level: UL */
nav > ul > li > ul::before {
	content: "";
	display: block;
	position: absolute;
	z-index: 1;
	left: 23px;
	top: 0;
	bottom: 0;
	border-left: 1px solid #7A7A7A;
}

/* Second level: UL LI */
nav > ul ul li::before {
	content: "";
	display: block;
	position: absolute;
	width: 8px;
	left: 23px;
	top: 16px;
	border-top: 1px solid #7A7A7A;
	z-index: 1;
}

/* Third level: UL LI */
nav > ul ul ul li::before {
	content: "";
	display: block;
	position: absolute;
	width: 18px;
	left: 10px;
	top: 17px;
	border-top: 1px solid transparent;
}

/* Second level and onwards > */
nav > ul ul li a i, {
font-size: @asideNavFontSize !important;
width: 18px !important;
text-align: center !important;
vertical-align:0 !important;
line-height:1 !important;
}

/* Second and Third level line color */
nav > ul > li:hover > ul::before, nav > ul > li:hover > ul > li::before {
	border-color: #ACACAC !important;
}

/* Second Level: UL */
nav ul ul {
	margin: 0;
	padding: 0;
	display: none;
	background: rgba(69, 69, 69,.6);
}

nav ul ul {
	padding: 7px 0;
}

/* Third Level: UL */
nav ul ul ul {
	background: transparent;
	padding: 0;
}

/* Third Level: LI */
nav ul ul li {
	margin: 0;
	padding: 0;
}

/* Second Level: A */
nav ul ul li > a {
	padding-left: 42px;
	font-size: @asideNavFontSizeSecondLevel;
	font-weight: normal;
	outline: 0;
}

/* Second Level: A:hover */
nav ul ul li > a:hover {
	background-color: rgba(69, 69, 69,.8);
	color: @white;
}

/* Third Level: A */
nav ul ul ul li a {
	color: @gray-mid-light;
	padding: 8px 10px 8px 60px;
	font-size: @asideNavFontSizeThirdLevel;
}

/* Third Level: A:hover */
nav ul ul ul li a:hover {
	color: @white;
}

/* Fourth Level: A */
nav ul ul ul ul li a {
	padding-left: 80px;
}

/* Fifth Level: A */
nav ul ul ul ul ul li a {
	padding-left: 100px;
}

/* Sixth Level: A */
nav ul ul ul ul ul ul li a {
	padding-left: 120px;
}

/* Second level: B */
nav ul ul b {
	color: #B3B3B3;
}

/* Second level: LI:hover B */
nav ul ul > li:hover b {
	color: #D5D9E2;
}

/*
 * HEADER NAV DROPDOWN LIST
 */

.header-dropdown-list {
	list-style: none;
	float: right;
	position: relative;
	margin: 0px; 
	padding-left: 0px;
	padding-right: 0px;
		& > li {

			display: inline-block;
			padding-right: 5px; 

			& > .dropdown-toggle {

				margin-top: 16px;
				display: block;
		}
	}
}

/*
 * ***************************************************
 * MAIN LAYOUT END
 * ***************************************************
 */

/*
 * ACCORDIONS
 */

.smart-accordion-default.panel-group {
	margin-bottom: 0px;
}

.smart-accordion-default.panel-group .panel+ .panel {
	margin-top: -1px;
}

.smart-accordion-default.panel-group .panel-heading {
	padding: 0px;
}

.smart-accordion-default.panel-group .panel-title a {
	display: block;
	padding: 10px 15px;
	text-decoration: none !important;
}

.smart-accordion-default .panel-heading, .panel-group .panel {
	border-radius: 0px;
	-webkit-border-radius: 0px;
	-moz-border-radius: 0px;
}

.smart-accordion-default .panel-default > .panel-heading {
	background-color: lighten( @gray-lightest, 3.5%);
}

.smart-accordion-default  .panel-default {
	border-color: lighten( @gray-light, 16.5%);
}

.smart-accordion-default  .panel-title > a > :first-child {
	display: none;
}

.smart-accordion-default  .panel-title > a.collapsed > .fa {
	display: none;
}

.smart-accordion-default  .panel-title > a.collapsed > :first-child {
	display: inline-block;
}

.no-padding .smart-accordion-default > div {
	border-left: none !important;
	border-right: none !important;
}

.no-padding .smart-accordion-default > div:first-child {
	border-top: none !important;
}

.no-padding .smart-accordion-default > div:last-child {
	border-bottom: none !important;
}

/*
 * ON OFF SWITCHES
 */

/* http://proto.io/freebies/onoff/ */

.onoffswitch-container {
	margin-top: 4px;
	margin-left: 7px;
	display: inline-block;
}

.onoffswitch {
	position: relative;
	width: 50px;
