}

.minified #hide-menu {
	display: none;
}

.btn-header.pull-right {
	margin-left: 6px;
}

.btn-header a > span {
	font-size: 13px;
	font-weight: normal;
	line-height: 30px;
	height: 30px;
	display: inline-block;
}

.btn-header > :first-child > a {
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	border-radius: 2px;
	cursor: default !important;
	display: inline-block;
	font-weight: bold;
	height: 30px;
	line-height: 24px;
	min-width: 30px;
	padding: 2px;
