}

.hidden-menu #hide-menu > :first-child > a:active, .full-screen #fullscreen > :first-child > a:active {
	-webkit-box-shadow: inset 0px 0px 6px 1px rgba(red(@black), green(@black), blue(@black), .2);
	box-shadow: inset 0px 0px 6px 1px rgba(red(@black), green(@black), blue(@black), .2);
}

/*
 * AJAX DROPDOWN
 */

.ajax-dropdown > :first-child {
	margin: 0 0 3px 0;
	padding: 0 0 9px 0;
}

.ajax-dropdown {
	position: absolute;
	display: none;
	z-index: @ajax-dropdown-zindex;
	background: white;
	top: 48px;
	left: 16px;
	width: 344px;
	height: 435px;
	border-radius: 0px;
	-webkit-border-radius: 0px;
	-moz-border-radius: 0px;
	-webkit-box-shadow: 0 2px 4px rgba(30, 30, 100, 0.25);
	-moz-box-shadow: 0 2px 4px rgba(30,30,100,0.25);
	box-shadow: 0 2px 4px rgba(30, 30, 100, 0.25);
	padding: 10px;
	background: @white;
	border: 1px solid #b3b3b3;
}

.ajax-dropdown:after, .ajax-dropdown:before {
	bottom: 100%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.ajax-dropdown:after {
	border-color: rgba(red(@white), green(@white), blue(@white), 0);
	border-bottom-color: @ajaxdrop-arrow-top-color;
	border-width: @ajaxdropdown-arrow-size;
	left: 50%;
	margin-left: -@ajaxdropdown-arrow-size;
}

.ajax-dropdown:before {
	border-color: rgba(131, 131, 131, 0);
	border-bottom-color: @ajaxdrop-arrow-bottom-color;
	border-width: @ajaxdropdown-arrow-bottom;
	left: 50%;
	margin-left: -@ajaxdropdown-arrow-bottom;
}

.ajax-dropdown .btn-group .btn {
	font-weight: bold;
	text-transform: capitalize;
}

/* better for 3 childs*/

.ajax-dropdown .btn-group > :nth-child(2) {
	border-right-width: 0px;
	border-left-width: 0px;
}

.ajax-dropdown .btn-group > :last-child {
	border-right-width: 1px !important;
}

/* better for 4+ childs
 .ajax-dropdown .btn-group >:first-child {
 border-right-width:1px;
 }

 .ajax-dropdown .btn-group >:nth-child(n+2) {
 border-left-width:0px;
 }

 .ajax-dropdown .btn-group >:last-child {
 border-right-width:1px !important;
 }	*/

.ajax-dropdown .btn-group .btn:active {
	top: 0px;
	left: 0px;
}

.ajax-notifications {
	height: @ajaxdropdown-height;
	display: block;
	overflow: auto;
	margin-right: -10px;
	margin-left: -10px;
	border-bottom: 1px solid #CECECE;
	border-top: 1px solid #CECECE;
	background: #E9E9E9;
}

.ajax-notifications  .alert.alert-transparent {
	background-color: transparent;
	border-color: transparent;
	color: #757575;
	margin: 13px 10px;
}

.ajax-notifications  .alert > :first-child {
	margin-bottom: 10px;
	font-weight: 400;
}

.ajax-dropdown > :last-child {
	font-size: 13px;
	display: block;
	padding: 5px 0;
	line-height: 22px;
	font-weight: 400;
}

.ajax-dropdown .fa-4x.fa-border {
	border-width: 3px;
	border-radius: 50%;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	display: block;
	margin: 0 auto;
	width: 46px;
	text-align: center;
	color: #D1D1D1;
	border-color: #D1D1D1;
}

/*
 * LARGE DROPDOWNS
 */

.dropdown-large {
	position: static !important;
}
.dropdown-menu-large {
	margin-left: 16px;
	margin-right: 16px;
	padding: 20px 0px;
}
.dropdown-menu-large > li > ul {
	padding: 0;
	margin: 0;
}
.dropdown-menu-large > li > ul > li {
	list-style: none;
}
.dropdown-menu-large > li > ul > li > a {
	display: block;
	padding: 3px 20px;
	clear: both;
	font-weight: normal;
	line-height: 1.428571429;
	color: #333333;
	white-space: normal;
}
.dropdown-menu-large > li ul > li > a:hover, .dropdown-menu-large > li ul > li > a:focus {
	text-decoration: none;
	color: #262626;
	background-color: #f5f5f5;
}
.dropdown-menu-large .disabled > a, .dropdown-menu-large .disabled > a:hover, .dropdown-menu-large .disabled > a:focus {
	color: #999999;
}
.dropdown-menu-large .disabled > a:hover, .dropdown-menu-large .disabled > a:focus {
	text-decoration: none;
	background-color: transparent;
	background-image: none;
	filter: progid:DXImageTransform.Microsoft.gradient(enabled = false);
	cursor: not-allowed;
}
.dropdown-menu-large .dropdown-header {
	color: #428bca;
	font-size: 18px;
}
@media (max-width: 768px) {
	.dropdown-menu-large {
		margin-left: 0;
		margin-right: 0;
	}
	.dropdown-menu-large > li {
		margin-bottom: 30px;
	}
	.dropdown-menu-large > li:last-child {
		margin-bottom: 0;
	}
	.dropdown-menu-large .dropdown-header {
		padding: 3px 15px !important;
	}
}

/*
 * PAGING
 */

.pagination-xs > li > a, .pagination-xs > li > span {
	padding: 4px 9px;
	font-size: 12px;
}

/*
 * NOTIFICATIONS : MAIL
 */

.notification-body *, .notification-body:before *, .notification-body:after * {
	box-sizing: border-box !important;
	-webkit-box-sizing: border-box !important;
	-moz-box-sizing: border-box !important;
}

.notification-body {
	padding: 0 0 0 3px;
	margin: 0;
	list-style: none;
}

.notification-body > li {
	border-bottom: 1px solid #E9E9E9;
	position: relative;
}

.notification-body > li > span {
	background: @white;
	display: block;
	min-height: 25px;
	overflow: hidden;
	padding: 8px 10px 13px;
	white-space: normal;
}

.notification-body > li > span:hover {
	background: #F0F4F7;
	color: #667F8F;
}

.notification-body > li a.msg {
	padding-left: 50px !important;
}

.notification-body > li a, .notification-body > li a:hover {
	text-decoration: none;
}

.notification-body > li a, .notification-body > li a span {
	display: block;
	position: relative;
	overflow: hidden;
}

.notification-body > li a, .notification-body > li a:hover {
	text-decoration: none;
}

.notification-body > li:hover .progress {
	background: @white;
	box-shadow: 0 1px 0 rgba(red(@black), green(@black), blue(@black), 0), 0 0 0 1px #A7A7A7 inset;
	-webkit-box-shadow: 0 1px 0 rgba(red(@black), green(@black), blue(@black), 0), 0 0 0 1px #CCC inset;
	-moz-box-shadow: 0 1px 0 transparent, 0 0 0 1px lighten(@gray-lighter, 10%) inset;
}
.notification-body > li:hover .text-muted {
	color: @gray-dark;
}

.notification-body .from {
	font-size: @font-size-base  + 1px;
	line-height: normal;
	color: @gray-dark;
	font-weight: normal;
	margin-right: 60px;
	margin-bottom: 6px;
	height: 20px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.notification-body .unread .from {
	font-weight: bold;
}

.notification-body .unread {
	background: #FFFFE0;
}

.notification-body time {
	position: absolute;
	top: 3px;
	right: 0;
	font-size: 11px;
	font-weight: normal;
	color: #058dc7;
}

.notification-body .msg-body, .notification-body .subject {
	font-size: @font-size-base;
	color: #A0A0A0;
	max-height: 35px;
	width: 100%;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.notification-body .subject {
	font-size: @font-size-base;
	color: @gray-dark;
	max-height: 22px;
}

/*
 * WIZARDS
 */

.bootstrapWizard {
	display: block;
	list-style: none;
	padding: 0;
	position: relative;
	width: 100%;
}
.bootstrapWizard a:hover, .bootstrapWizard a:active, .bootstrapWizard a:focus {
	text-decoration: none
}
.bootstrapWizard li {
	display: block;
	float: left;
	width: 25%;
	text-align: center;
	padding-left: 0;
}
.bootstrapWizard li:before {
	border-top: 3px solid #55606E;
	content: "";
	display: block;
	font-size: 0;
	overflow: hidden;
	position: relative;
	top: 11px;
	right: 1px;
	width: 100%;
	z-index: 1;
}
.bootstrapWizard li:first-child:before {
	left: 50%;
	max-width: 50%;
}
.bootstrapWizard li:last-child:before {
	max-width: 50%;
	width: 50%;
}

.bootstrapWizard li.complete .step {
	background: @wizard-step-complete;
	padding: 1px 6px;
	border: 3px solid #55606E;
}
.bootstrapWizard li  .step i {
	font-size: 10px;
	font-weight: normal;
	position: relative;
	top: -1.5px;
}
.bootstrapWizard li .step {
	background: #B2B5B9;
	color: @white;
	display: inline;
	font-size: 15px;
	font-weight: bold;
	line-height: 12px;
	padding: 7px 13px;
	border: 3px solid transparent;
	border-radius: 50%;
	line-height: normal;
	position: relative;
	text-align: center;
	z-index: 2;
	transition: all 0.1s linear 0s;
}
.bootstrapWizard li.active .step, .bootstrapWizard li.active.complete .step {
	background: @wizard-step-active;
	color: @white;
	font-weight: bold;
	padding: 7px 13px;
	font-size: 15px;
	border-radius: 50%;
	border: 3px solid #55606E;
}
.bootstrapWizard li.complete .title, .bootstrapWizard li.active .title {
	color: #2B3D53;
}
.bootstrapWizard li .title {
	color: @gray-mid-light;
	display: block;
	font-size: 13px;
	line-height: 15px;
	max-width: 100%;
	position: relative;
	table-layout: fixed;
	text-align: center;
	top: 20px;
	word-wrap: break-word;
	z-index: 104;
}

.wizard-actions {
	display: block;
	list-style: none;
	padding: 0;
	position: relative;
	width: 100%;
}
.wizard-actions li {
	display: inline;
}

.tab-content.transparent {
	background-color: transparent;
}

/*
 * FUELUX WIZARD
 */

.fuelux .wizard {
	position: relative;
	overflow: hidden;
	background-color: #f9f9f9;
	border: 1px solid #d4d4d4;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	border-radius: 4px;
	*zoom: 1;
	-webkit-box-shadow: 0 1px 4px rgba(red(@black), green(@black), blue(@black), 0.065);
	-moz-box-shadow: 0 1px 4px rgba(red(@black), green(@black), blue(@black), 0.065);
	box-shadow: 0 1px 4px rgba(red(@black), green(@black), blue(@black), 0.065);
}

.fuelux .wizard:before, .fuelux .wizard:after {
	display: table;
	line-height: 0;
	content: "";
}

.fuelux .wizard:after {
	clear: both;
}

.fuelux .wizard ul {
	width: 4000px;
	padding: 0;
	margin: 0;
	list-style: none outside none;
}

.fuelux .wizard ul.previous-disabled li.complete {
	cursor: default;
}

.fuelux .wizard ul.previous-disabled li.complete:hover {
	color: @green;
	cursor: default;
	background: #f3f4f5;
}

.fuelux .wizard ul.previous-disabled li.complete:hover .chevron:before {
	border-left-color: #f3f4f5;
}

.fuelux .wizard ul li {
	position: relative;
	float: left;
	height: 46px;
	padding: 0 20px 0 30px;
	margin: 0;
	font-size: 16px;
	line-height: 46px;
	color: #999999;
	cursor: default;
	background: #ededed;
}

.fuelux .wizard ul li .chevron {
	position: absolute;
	top: 0;
	right: -14px;
	z-index: 1;
	display: block;
	border: 24px solid transparent;
	border-right: 0;
	border-left: 14px solid #d4d4d4;
}

.fuelux .wizard ul li .chevron:before {
	position: absolute;
	top: -24px;
	right: 1px;
	display: block;
	border: 24px solid transparent;
	border-right: 0;
	border-left: 14px solid #ededed;
	content: "";
}

.fuelux .wizard ul li.complete {
	color: @state-success-text;
	background: #f3f4f5;
}

.fuelux .wizard ul li.complete:hover {
	cursor: pointer;
	background: #e7eff8;
}

.fuelux .wizard ul li.complete:hover .chevron:before {
	border-left: 14px solid #e7eff8;
}

.fuelux .wizard ul li.complete .chevron:before {
	border-left: 14px solid #f3f4f5;
}

.fuelux .wizard ul li.active {
	color: #3a87ad;
	background: #f1f6fc;
}

.fuelux .wizard ul li.active .chevron:before {
	border-left: 14px solid #f1f6fc;
}

.fuelux .wizard ul li .badge {
	margin-right: 8px;
}

.fuelux .wizard ul li:first-child {
	padding-left: 20px;
	border-radius: 4px 0 0 4px;
}

.fuelux .wizard .actions {
	position: absolute;
	right: 0;
	z-index: 2;
	float: right;
	padding-right: 15px;
	padding-left: 15px;
	line-height: 46px;
	vertical-align: middle;
	background-color: @gray-lighter;
	border-left: 1px solid #d4d4d4;
}

.fuelux .wizard .actions a {
	margin-right: 8px;
	font-size: 12px;
	line-height: 45px;
}

.fuelux .wizard .actions .btn-prev i {
	margin-right: 5px;
}

.fuelux .wizard .actions .btn-next i {
	margin-left: 5px;
}

.fuelux .step-content .step-pane {
	display: none;
}

.fuelux .step-content .active {
	display: block;
}

.fuelux .step-content .active .btn-group .active {
	display: inline-block;
}

/*
 * jarvismetro TILE
 */

